body, html {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: #09090b;
}

.concerts-bg {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(0deg, rgba(30,42,62,1) 0%, rgba(54,69,98,0.10) 100%);
  -webkit-backdrop-filter: blur(12.5px);
  backdrop-filter: blur(12.5px);
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.sign {
  text-align: center;
  font-family: 'Clip', monospace;
  font-size: 3em;
  color: #ffe6ff;
  text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd, -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd, 0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
  margin-top: 40px;
  margin-bottom: 40px;
  letter-spacing: 2px;
}

.concert-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin: 0 auto 40px auto;
  max-width: 1600px;
}

.concert-card {
  width: 350px;
  min-height: 540px;
  margin: 0;
  box-shadow: 0 2px 16px 0 rgba(0,0,0,0.12);
  border-radius: 16px;
  background: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: fadeIn 1s;
  position: relative;
}

.concert-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}

.concert-card h3 {
  margin: 18px 0 8px 0;
  padding: 0 18px;
  color: #b8005a;
  font-size: 1.5em;
  font-weight: 700;
}

.concert-card p {
  padding: 0 18px;
  margin: 0 0 10px 0;
  color: #232526;
  font-size: 1.1em;
}

.ticket-booking {
  padding: 18px;
  border-top: 1px solid #eee;
  background: #fafafa;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.concert-dropdown {
  position: relative;
  display: inline-block;
  margin-bottom: 12px;
}

.dropdown-btn {
  background: #b702fe;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 8px;
  transition: background 0.3s;
}
.dropdown-btn:hover {
  background: #02b7fe;
}

.dropdown-content {
  display: none;
  position: absolute;
  background: #f9f9f9;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 16px;
  z-index: 10;
  border-radius: 12px;
  top: 46px;
  left: 0;
}

.concert-dropdown.open .dropdown-content {
  display: block;
}

.quantity-counter {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.ticket-count {
  width: 60px;
  padding: 8px;
  text-align: center;
  border-radius: 8px;
  border: 1px solid #b8005a;
  font-size: 1.1em;
  margin-left: 8px;
}

.book-now {
  background: #b702fe;
  color: #fff;
  padding: 12px 0;
  border: none;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1.1em;
  cursor: pointer;
  margin-top: 16px;
  transition: background 0.3s;
}
.book-now:hover {
  background: #02b7fe;
}

.total-cost {
  font-weight: bold;
  color: #b8005a;
}

/* Responsive, flexible, 4:5 ratio ticket modal */
.ticket-details-modal {
  aspect-ratio: 4 / 5;
  width: 90vw;
  max-width: 420px;
  min-width: 260px;
  max-height: 92vh;
  background: linear-gradient(120deg, #fff 90%, #ffe6ff 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(58,28,113,0.20);
  padding: 24px 18px 18px 18px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  overflow-y: auto;
  position: relative;
}

.ticket-details-header {
  text-align: center;
  color: #b8005a;
  font-size: 2em;
  font-weight: 700;
  margin-bottom: 12px;
  letter-spacing: 1px;
}

.ticket-details-body {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 10px;
}

.ticket-details-col {
  font-size: 1.08em;
  color: #232526;
  background: rgba(186,0,90,0.03);
  border-radius: 10px;
  padding: 10px 12px;
  margin-bottom: 4px;
}
.ticket-details-col p {
  margin-bottom: 6px;
  line-height: 1.4;
}

.ticket-qr-canvas {
  text-align: center;
  margin: 10px 0 0 0;
}

.ticket-modal-btns {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 16px;
  width: 100%;
}

.ticket-modal-btns button {
  border-radius: 10px;
  font-weight: 700;
  font-size: 1.08em;
  padding: 10px 0;
  letter-spacing: 0.5px;
}

@media (max-width: 600px) {
  .ticket-details-modal {
    max-width: 98vw;
    min-width: 0;
    padding: 8px 2px 12px 2px;
    border-radius: 10px;
  }
  .ticket-details-header {
    font-size: 1.2em;
  }
  .ticket-details-col {
    font-size: 0.97em;
    padding: 7px 5px;
  }
  .ticket-modal-btns button {
    font-size: 0.98em;
    padding: 8px 0;
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
