.collab-container {
  /* Remove background and box-shadow for transparent look */
  background: transparent;
  color: #000;
  border-radius: 0;
  box-shadow: none;
  padding: 32px 24px 24px 24px;
  margin: 32px auto;
  max-width: 1400px;
  width: 95vw;
  aspect-ratio: 16 / 9;
  min-width: 320px;
  min-height: 320px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.collab-tabs {
  display: flex;
  justify-content: flex-start;
  gap: 24px;
  margin-bottom: 24px;
}

.collab-tab {
  background: #ffb6c1;
  color: #b8005a;
  border: none;
  border-radius: 8px 8px 0 0;
  padding: 12px 32px;
  font-size: 1.13em;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
  outline: none;
  box-shadow: 0 2px 6px rgba(255,182,193,0.10);
}

.collab-tab.active {
  background: #ff69b4;
  color: #fff;
}

.collab-section {
  background: #fff0f6;
  border-radius: 0 0 18px 18px;
  padding: 32px 24px;
  min-height: 180px;
  color: #b8005a;
  box-shadow: 0 2px 12px rgba(255,182,193,0.13);
  font-size: 1.1em;
}

.collab-heading {
  font-size: 2.1em;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.5px;
  color: #b8005a;
  margin-bottom: 18px;
  margin-top: 2px;
  font-family: 'Segoe UI', Arial, sans-serif;
  text-shadow: 0 2px 10px rgba(0,0,0,0.13);
}

.collab-bg {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(0deg, rgba(30,42,62,1) 0%, rgba(54,69,98,0.10) 100%);
  -webkit-backdrop-filter: blur(12.5px);
  backdrop-filter: blur(12.5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

@media (max-width: 1400px) {
  .collab-container {
    max-width: 99vw;
    width: 99vw;
    aspect-ratio: 16 / 9;
    padding: 14px 5px 10px 5px;
    margin: 12px 0;
  }
}

@media (max-width: 900px) {
  .collab-container {
    max-width: 100vw;
    width: 100vw;
    aspect-ratio: 16 / 9;
    padding: 3vw 1vw 3vw 1vw;
    margin: 0;
    min-width: 0;
    min-height: 0;
  }
  .collab-tabs {
    gap: 12px;
    margin-bottom: 10px;
  }
  .collab-tab {
    padding: 8px 12px;
    font-size: 0.98em;
  }
  .collab-section {
    padding: 10px 4px;
    font-size: 0.97em;
  }
  .collab-heading {
    font-size: 1.2em;
    margin-bottom: 8px;
  }
}

@media (max-width: 600px) {
  .collab-container {
    max-width: 100vw;
    width: 100vw;
    aspect-ratio: 16 / 9;
    padding: 5vw 1vw 5vw 1vw;
    margin: 0;
    min-width: 0;
    min-height: 0;
  }
  .collab-tabs {
    gap: 8px;
    margin-bottom: 8px;
  }
  .collab-tab {
    padding: 6px 8px;
    font-size: 0.9em;
  }
  .collab-section {
    padding: 8px 2px;
    font-size: 0.9em;
  }
  .collab-heading {
    font-size: 1.1em;
    margin-bottom: 6px;
  }
}
