/* Subscriptions Page Styling */
.subs-bg {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(0deg, rgba(30,42,62,1) 0%, rgba(54,69,98,0.10) 100%);
  -webkit-backdrop-filter: blur(12.5px);
  backdrop-filter: blur(12.5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}

.subs-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  gap: 32px;
  margin-bottom: 32px;
  flex-wrap: wrap;
}

.subs-card {
  width: 320px;
  min-height: 470px;
  border-radius: 15px 15px 0 0;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: transparent;
  box-shadow: 0 2px 8px #fcb69f55;
  transition: transform 0.3s, box-shadow 0.3s;
}
.subs-card:hover {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 6px 20px;
  transform: scale(1.04);
}

.subs-card__info {
  width: 100%;
  height: 130px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 0;
}
.subs-card__info::before {
  background-color: rgba(194,148,102,0.8);
  width: 100%;
  height: 100%;
  opacity: 0.8;
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

.subs-card__name {
  color: #fff;
  margin-bottom: 10px;
  font-size: 32px;
  font-weight: 400;
  letter-spacing: 1px;
}

.subs-card__price {
  font-size: 22px;
  font-weight: 400;
}

.subs-card__priceSpan {
  color: #aaa;
}

.subs-card__content {
  background-color: rgba(251,251,251,0.85);
  width: 100%;
  height: calc(100% - 130px);
  border-top: solid 2px;
  border-bottom: solid 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subs-card__rows {
  width: 100%;
}

.subs-card__row {
  color: #aaa;
  width: 100%;
  padding: 20px 0;
  text-align: center;
}

.subs-card__row:nth-of-type(odd) {
  background-color: #f4f4f4;
}
.subs-card__row:nth-of-type(even) {
  background-color: #fbfbfb;
}

.subs-card__link {
  color: #fff;
  padding: 10px 15px;
  margin: 20px 0;
  text-decoration: none;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
}

.form-container {
  background-color: rgba(255,255,255,0.92);
  border-radius: 10px;
  padding: 20px;
  max-width: 320px;
  margin: 20px auto;
  z-index: 10;
  box-shadow: 0 2px 8px #fcb69f55;
}
.form-container label {
  display: block;
  margin-bottom: 10px;
}
.form-container input[type="text"],
.form-container input[type="email"],
.form-container input[type="tel"],
.form-container input[type="number"] {
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.form-container button {
  width: 100%;
  padding: 10px 0;
  background-color: #c29466;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  font-weight: 600;
  font-size: 16px;
}
.form-container button.cancel {
  background-color: #aaa;
  margin-top: 0;
}
