.player-body {
    width: 100%;
    height: 40%;
    margin: 3% 0%;
  }
  
  .player-left-body {
    width: 37%;
  }
  
  .player-right-body {
    width: 63%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  .song-title {
    text-align: center;
    font-size: 58px;
    font-weight: bold;
    margin: 0px;
    color: #c4d0e3;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  
  .song-artist {
    color: #9aa9c2;
    font-size: 14px;
    font-weight: 500;
  }
  
  .player-right-bottom {
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
  
  .song-duration {
    width: 50%;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center;
  }
  
  .duration {
    font-size: 20px;
    font-weight: 700;
    margin: 0;
    color: #c4d0e3;
  }




  .seek-bar {
    width: 70%;
    height: 10px;
    margin: 0 15px;
    -webkit-appearance: none;
    appearance: none;
    background: #9aa5b75f;
    border-radius: 5px;
    outline: none;
  }
  
  .seek-bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    background: #83e5f2;
    border-radius: 50%;
    cursor: pointer;
  }
  
  .seek-bar::-moz-range-thumb {
    width: 20px;
    height: 20px;
    background: #83e5f2;
    border-radius: 50%;
    cursor: pointer;
  }
  