.entry-body {
    width: 100%;
    align-items: center;
    margin-top: 10%;
  }
  
  .entry-image {
    height: 50px;
    width: 50px;
    border-radius: 15px;
    margin-right: 10px;
  }
  
  .entry-right-body {
    flex-direction: column;
    justify-content: center;
  }
  
  .entry-title {
    font-weight: 700;
    font-size: 16px;
    color: #a9f8df;
    margin: 0px 0px 5px;
  }
  
  .entry-subtitle {
    font-weight: 400;
    font-size: 12px;
    color: #8bf4f87c;
    margin: 0;
  }

  .play-icon {
    cursor: pointer;
    
  }